___CSS_LOADER_EXPORT___.push([module.id, `* {
  padding: 0;
  margin: 0;
}

body {
  font-family: "Quicksand", sans-serif;
  background-color: white;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  font-size: 18px;
}

/*
    AppBar
  */

.app-bar {
  padding: 8px 16px;
  background: linear-gradient(45deg, #e35c3b, #f4a261);
  display: grid;
  grid-template-columns: auto 1fr auto;
  gap: 10px;
  position: sticky;
  top: 0;
  z-index: 99;
  /* box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2); */
  height: 80px;
}

.app-bar .app-bar__menu {
  display: flex;
  align-items: center;
}

.app-bar .app-bar__menu button {
  background-color: transparent;
  border: none;
  font-size: 18px;
  padding: 8px;
  cursor: pointer;
}

.app-bar .app-bar__brand {
  display: flex;
  align-items: center;
  padding-left: 30px;
}

.app-bar .app-bar__brand img {
  width: auto;
  height: var(--logo-size);
}

.app-bar .app-bar__navigation {
  position: absolute;
  top: 50px;
  left: -180px;
  width: 150px;
  transition: all 0.3s;
  padding: 8px;
  overflow: hidden;
}

.app-bar .app-bar__navigation.open {
  left: 0;
}

.app-bar .app-bar__navigation ul li a {
  display: inline-block;
  text-decoration: none;
  color: white;
  padding: 15px;
  margin-bottom: 5px;
  width: 100%;
}

.app-bar .app-bar__navigation ul li a:hover {
  font-weight: bold;
}

.app-bar .app-bar__navigation ul li .icon-button {
  background-color: transparent;
  color: white;
  border: none;
  cursor: pointer;
  padding: 8px;
  opacity: 70%;
}

.app-bar .app-bar__navigation ul li .icon-button:hover {
  opacity: 100%;
}

.app-bar .app-bar__navigation ul li .icon-button i {
  font-size: 20px;
}

/* Jumbotron */

.jumbotron {
  background: linear-gradient(45deg, #e35c3b, #f4a261);
  height: 450px;
}

.hero {
  display: flex;
  align-items: center;
  min-height: 380px;
  width: 100%;
  text-align: left;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-size: 600px;
  background-repeat: no-repeat;
  background-position: right bottom;
  height: 450px;
}

.hero__inner {
  margin: 0 auto;
  max-width: 800px;
  margin-left: 60px;
  line-height: 1, 2;
}

.hero__title {
  color: #fff;
  font-weight: 500;
  font-size: 40px;
  text-align: left;
  font-weight: bold;
  opacity: 0;
  animation: fadeIn 1s forwards;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.hero__tagline {
  color: #fff;
  margin-top: 30px;
  font-size: 28px;
  font-weight: 300;
  margin-bottom: 30px;
  list-style-type: none;
}

.hero__button {
  margin-right: 10px;
  background-color: #f4a261;
  border-radius: 25px;
  padding: 8px;
  padding-left: 35px;
  padding-right: 35px;
  text-decoration: none;
  color: white;
  font-weight: bold;
  font-size: 20px;
  transition: background-color 0.3s ease;
}

.hero__button:hover {
  background-color: #b03e21;
}

/*
    Main Content
  */

main {
  flex: 1;
}

.content {
  margin: 0 auto;
  min-height: 100%;
}

.content .content__heading {
  font-weight: normal;
}

/*
    Footer
  */

footer {
  background: linear-gradient(45deg, #e35c3b, #f4a261);
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: white;
  padding: 60px;
  padding-top: 40px;
  padding-bottom: 40px;
}

.footer__left img {
  padding-bottom: 10px;
}

.footer__left .description {
  margin: 0;
  font-size: 18px;
}

.footer__left .copyright {
  margin: 0;
  font-size: 14px;
  padding-top: 40px;
}

.social-media {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
  display: block;
}

.social-media li {
  margin-right: 10px;
  background-color: #e35c3b;
  border-radius: 25px;
  padding: 8px;
  padding-left: 35px;
  padding-right: 35px;
  margin-bottom: 5px;
  display: block;
  transition: background-color 0.3s;
}

.social-media a {
  color: white;
  text-decoration: none;
  font-size: 16px;
  transition: color 0.3s;
}

.social-media a:hover {
  color: #f4a261;
}

.social-media li:hover {
  background-color: #b03e21;
}

.social-media i {
  font-size: 18px;
  padding: 5px;
}

/*Campaign*/
.campaign {
  padding: 100px 150px 50px 150px;
  background-color: white;
}

.campaign p {
  text-align: center;
  color: #f4a261;
  font-size: 20px;
  font-weight: bold;
}

.campaign h1 {
  text-align: center;
  color: #b03e21;
  font-size: 35px;
  font-weight: bold;
}

.Campaign_List {
  padding-top: 30px;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
}

.CardCampaign {
  margin: 20px;
  padding-top: 20px;
  padding-bottom: 20px;
  align-items: center;
  text-align: center;
  background-color: #fff;
  border-radius: 25px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.campaign_image {
  padding: 25px;
  display: block;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
  background-repeat: no-repeat;
  background-position: top center;
  background-size: 30%;
}

.container_campaign h2 {
  padding: 10px 25px 20px 25px;
  color: #b03e21;
  font-size: 25px;
  font-weight: bold;
}

/*Fitur Unggulan*/
.fitur {
  padding: 100px 50px 100px 50px;
  background-color: white;
}

.fitur h1 {
  text-align: center;
  color: #b03e21;
  font-size: 35px;
  font-weight: bold;
}

.Fitur_list {
  padding-top: 50px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}

.Card_Fitur {
  margin: auto;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}

.Card_img {
  padding: 20px;
  display: block;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_2___});
  background-repeat: no-repeat;
  background-position: center;
  background-size: 50%;
}

.Text_Fitur {
  text-align: left;
}

.Text_Fitur h2 {
  color: #e35c3b;
  font-size: 24px;
  font-weight: bold;
}

.Text_Fitur p {
  color: gray;
  font-size: 18px;
  font-weight: bold;
}

/*Donasi*/
.donasi {
  padding: 100px;
  background-color: white;
}

.donasi p {
  text-align: left;
  color: #f4a261;
  font-size: 20px;
  font-weight: bold;
}

.donasi h1 {
  text-align: left;
  color: #b03e21;
  font-size: 35px;
  font-weight: bold;
}

.Donasi_list {
  padding-top: 30px;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  /* grid-template-rows: repeat(3, 1fr); */
}

.Card_Donasi {
  margin: 20px;
  padding-top: 20px;
  padding-bottom: 20px;
  align-items: center;
  background-color: #e35c3b;
  border-radius: 30px 0px 30px 0px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.Donasi_img {
  padding: 25px;
  display: flex;
  justify-content: center;
  /* background-image: url("../public/campaign.png"); */
  background-repeat: no-repeat;
  background-position: top center;
  background-size: 30%;
}

.Container_Donasi h4 {
  padding: 10px 10px 0px 20px;
  color: white;
  text-align: left;
  font-size: 20px;
  font-weight: bold;
}

.Container_Donasi p {
  padding: 0px 10px 10px 20px;
  color: white;
  text-align: left;
  font-size: 15px;
}

.Container_Donasi h3 {
  padding: 10px 10px 10px 20px;
  color: white;
  text-align: left;
  font-size: 25px;
  font-weight: bold;
}

/*Ulasan*/
.ulasan {
  padding: 100px 250px 100px 250px;
  background-color: white;
}

.ulasan p {
  text-align: center;
  color: #f4a261;
  font-size: 20px;
  font-weight: bold;
}

.ulasan h1 {
  text-align: center;
  color: #b03e21;
  font-size: 35px;
  font-weight: bold;
  padding-bottom: 30px;
}

.CardUlasan {
  margin: 20px;
  padding-top: 20px;
  padding-bottom: 20px;
  align-items: center;
  text-align: center;
  background-color: #fff;
  border-radius: 25px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.ulasan_image {
  padding: 15px;
  display: block;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_3___});
  background-repeat: no-repeat;
  background-position: top center;
  background-size: 7%;
}

.container_ulasan p {
  padding: 20px 30px 20px 30px;
  color: grey;
  font-size: 15px;
  font-style: italic;
}

.container_ulasan h5 {
  padding: 20px 30px 20px 30px;
  color: grey;
  font-size: 25px;
  font-weight: bold;
}
