ton i {\r\n  font-size: 20px;\r\n}\r\n\r\n/* Jumbotron */\r\n\r\n.jumbotron {\r\n  background: linear-gradient(45deg, #e35c3b, #f4a261);\r\n  height: 450px;\r\n}\r\n\r\n.hero {\r\n  display: flex;\r\n  align-items: center;\r\n  min-height: 380px;\r\n  width: 100%;\r\n  text-align: left;\r\n  background-image: url(\"../public/ornamen.png\");\r\n  background-size: 600px;\r\n  background-repeat: no-repeat;\r\n  background-position: right bottom;\r\n  height: 450px;\r\n}\r\n\r\n.hero__inner {\r\n  margin: 0 auto;\r\n  max-width: 800px;\r\n  margin-left: 60px;\r\n  line-height: 1, 2;\r\n}\r\n\r\n.hero__title {\r\n  color: #fff;\r\n  font-weight: 500;\r\n  font-size: 40px;\r\n  text-align: left;\r\n  font-weight: bold;\r\n  opacity: 0;\r\n  animation: fadeIn 1s forwards;\r\n}\r\n\r\n@keyframes fadeIn {\r\n  from {\r\n    opacity: 0;\r\n    transform: translateY(-20px);\r\n  }\r\n  to {\r\n    opacity: 1;\r\n    transform: translateY(0);\r\n  }\r\n}\r\n\r\n.hero__tagline {\r\n  color: #fff;\r\n  margin-top: 30px;\r\n  font-size: 28px;\r\n  font-weight: 300;\r\n  margin-bottom: 30px;\r\n  list-style-type: none;\r\n}\r\n\r\n.hero__button {\r\n  margin-right: 10px;\r\n  background-color: #f4a261;\r\n  border-radius: 25px;\r\n  padding: 8px;\r\n  padding-left: 35px;\r\n  padding-right: 35px;\r\n  text-decoration: none;\r\n  color: white;\r\n  font-weight: bold;\r\n  font-size: 20px;\r\n  transition: background-color 0.3s ease;\r\n}\r\n\r\n.hero__button:hover {\r\n  background-color: #b03e21;\r\n}\r\n\r\n/*\r\n    Main Content\r\n  */\r\n\r\nmain {\r\n  flex: 1;\r\n}\r\n\r\n.content {\r\n  margin: 0 auto;\r\n  min-height: 100%;\r\n}\r\n\r\n.content .content__heading {\r\n  font-weight: normal;\r\n}\r\n\r\n/*\r\n    Footer\r\n  */\r\n\r\nfooter {\r\n  background: linear-gradient(45deg, #e35c3b, #f4a261);\r\n  display: flex;\r\n  justify-content: space-between;\r\n  align-items: center;\r\n  color: white;\r\n  padding: 60px;\r\n  padding-top: 40px;\r\n  padding-bottom: 40px;\r\n}\r\n\r\n.footer__left img {\r\n  padding-bottom: 10px;\r\n}\r\n\r\n.footer__left .description {\r\n  margin: 0;\r\n  font-size: 18px;\r\n}\r\n\r\n.footer__left .copyright {\r\n  margin: 0;\r\n  font-size: 14px;\r\n  padding-top: 40px;\r\n}\r\n\r\n.social-media {\r\n  list-style-type: none;\r\n  margin: 0;\r\n  padding: 0;\r\n  display: flex;\r\n  display: block;\r\n}\r\n\r\n.social-media li {\r\n  margin-right: 10px;\r\n  background-color: #e35c3b;\r\n  border-radius: 25px;\r\n  padding: 8px;\r\n  padding-left: 35px;\r\n  padding-right: 35px;\r\n  margin-bottom: 5px;\r\n  display: block;\r\n  transition: background-color 0.3s;\r\n}\r\n\r\n.social-media a {\r\n  color: white;\r\n  text-decoration: none;\r\n  font-size: 16px;\r\n  transition: color 0.3s;\r\n}\r\n\r\n.social-media a:hover {\r\n  color: #f4a261;\r\n}\r\n\r\n.social-media li:hover {\r\n  background-color: #b03e21;\r\n}\r\n\r\n.social-media i {\r\n  font-size: 18px;\r\n  padding: 5px;\r\n}\r\n\r\n/*Campaign*/\r\n.campaign {\r\n  padding: 100px 150px 50px 150px;\r\n  background-color: white;\r\n}\r\n\r\n.campaign p {\r\n  text-align: center;\r\n  color: #f4a261;\r\n  font-size: 20px;\r\n  font-weight: bold;\r\n}\r\n\r\n.campaign h1 {\r\n  text-align: center;\r\n  color: #b03e21;\r\n  font-size: 35px;\r\n  font-weight: bold;\r\n}\r\n\r\n.Campaign_List {\r\n  padding-top: 30px;\r\n  display: grid;\r\n  grid-template-columns: repeat(4, 1fr);\r\n}\r\n\r\n.CardCampaign {\r\n  margin: 20px;\r\n  padding-top: 20px;\r\n  padding-bottom: 20px;\r\n  align-items: center;\r\n  text-align: center;\r\n  background-color: #fff;\r\n  border-radius: 25px;\r\n  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);\r\n}\r\n\r\n.campaign_image {\r\n  padding: 25px;\r\n  display: block;\r\n  background-image: url(\"../public/campaign.png\");\r\n  background-repeat: no-repeat;\r\n  background-position: top center;\r\n  background-size: 30%;\r\n}\r\n\r\n.container_campaign h2 {\r\n  padding: 10px 25px 20px 25px;\r\n  color: #b03e21;\r\n  font-size: 25px;\r\n  font-weight: bold;\r\n}\r\n\r\n/*Fitur Unggulan*/\r\n.fitur {\r\n  padding: 100px 50px 100px 50px;\r\n  background-color: white;\r\n}\r\n\r\n.fitur h1 {\r\n  text-align: center;\r\n  color: #b03e21;\r\n  font-size: 35px;\r\n  font-weight: bold;\r\n}\r\n\r\n.Fitur_list {\r\n  padding-top: 50px;\r\n  display: grid;\r\n  grid-template-columns: repeat(3, 1fr);\r\n}\r\n\r\n.Card_Fitur {\r\n  margin: auto;\r\n  display: grid;\r\n  grid-template-columns: repeat(2, 1fr);\r\n}\r\n\r\n.Card_img {\r\n  padding: 20px;\r\n  display: block;\r\n  background-image: url(\"../public/fitur.png\");\r\n  background-repeat: no-repeat;\r\n  background-position: center;\r\n  background-size: 50%;\r\n}\r\n\r\n.Text_Fitur {\r\n  text-align: left;\r\n}\r\n\r\n.Text_Fitur h2 {\r\n  color: #e35c3b;\r\n  font-size: 24px;\r\n  font-weight: bold;\r\n}\r\n\r\n.Text_Fitur p {\r\n  color: gray;\r\n  font-size: 18px;\r\n  font-weight: bold;\r\n}\r\n\r\n/*Donasi*/\r\n.donasi {\r\n  padding: 100px;\r\n  background-color: white;\r\n}\r\n\r\n.donasi p {\r\n  text-align: left;\r\n  color: #f4a261;\r\n  font-size: 20px;\r\n  font-weight: bold;\r\n}\r\n\r\n.donasi h1 {\r\n  text-align: left;\r\n  color: #b03e21;\r\n  font-size: 35px;\r\n  font-weight: bold;\r\n}\r\n\r\n.Donasi_list {\r\n  padding-top: 30px;\r\n  display: grid;\r\n  grid-template-columns: repeat(4, 1fr);\r\n  /* grid-template-rows: repeat(3, 1fr); */\r\n}\r\n\r\n.Card_Donasi {\r\n  margin: 20px;\r\n  padding-top: 20px;\r\n  padding-bottom: 20px;\r\n  align-items: center;\r\n  background-color: #e35c3b;\r\n  border-radius: 30px 0px 30px 0px;\r\n  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);\r\n}\r\n\r\n.Donasi_img {\r\n  padding: 25px;\r\n  display: flex;\r\n  justify-content: center;\r\n  /* background-image: url(\"../public/campaign.png\"); */\r\n  background-repeat: no-repeat;\r\n  background-position: top center;\r\n  background-size: 30%;\r\n}\r\n\r\n.Container_Donasi h4 {\r\n  padding: 10px 10px 0px 20px;\r\n  color: white;\r\n  text-align: left;\r\n  font-size: 20px;\r\n  font-weight: bold;\r\n}\r\n\r\n.Container_Donasi p {\r\n  padding: 0px 10px 10px 20px;\r\n  color: white;\r\n  text-align: left;\r\n  font-size: 15px;\r\n}\r\n\r\n.Container_Donasi h3 {\r\n  padding: 10px 10px 10px 20px;\r\n  color: white;\r\n  text-align: left;\r\n  font-size: 25px;\r\n  font-weight: bold;\r\n}\r\n\r\n/*Ulasan*/\r\n.ulasan {\r\n  padding: 100px 250px 100px 250px;\r\n  background-color: white;\r\n}\r\n\r\n.ulasan p {\r\n  text-align: center;\r\n  color: #f4a261;\r\n  font-size: 20px;\r\n  font-weight: bold;\r\n}\r\n\r\n.ulasan h1 {\r\n  text-align: center;\r\n  color: #b03e21;\r\n  font-size: 35px;\r\n  font-weight: bold;\r\n  padding-bottom: 30px;\r\n}\r\n\r\n.CardUlasan {\r\n  margin: 20px;\r\n  padding-top: 20px;\r\n  padding-bottom: 20px;\r\n  align-items: center;\r\n  text-align: center;\r\n  background-color: #fff;\r\n  border-radius: 25px;\r\n  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);\r\n}\r\n\r\n.ulasan_image {\r\n  padding: 15px;\r\n  display: block;\r\n  background-image: url(\"../public/user.png\");\r\n  background-repeat: no-repeat;\r\n  background-position: top center;\r\n  background-size: 7%;\r\n}\r\n\r\n.container_ulasan p {\r\n  padding: 20px 30px 20px 30px;\r\n  color: grey;\r\n  font-size: 15px;\r\n  font-style: italic;\r\n}\r\n\r\n.container_ulasan h5 {\r\n  padding: 20px 30px 20px 30px;\r\n  color: grey;\r\n  font-size: 25px;\r\n  font-weight: bold;\r\n}\r\n"],"sourceRoot":""}]);
